import React from 'react';
import { Link } from "gatsby";

import "./styles.scss";

const HeadlineCard = ({ data }) => {
    const {frontmatter} = data
    return (
        <div className={`BigHeadlineCard`}>
            <div className="imgContainer">
                <Link to={`/${frontmatter.slug}`} style={{"backgroundImage": `url(${frontmatter.cover_photo})`}}></Link>
            </div>

            <Link className="descriptionContainer" to={`/${frontmatter.slug}`}>
                <div className="title">
                    {frontmatter.title}
                </div>
            </Link>
        </div>
    );
};

export default HeadlineCard;
